@import url('https://fonts.googleapis.com/css2?family=Pushster&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Teko:wght@600&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Antic+Slab&family=Eczar:wght@600&family=Teko:wght@600&display=swap');
body{
    background-color: rgb(247, 242, 242);
    
    font-family: 'Eczar', serif;
}

.navbar-brand, .nav-item{
    font-family: 'Pushster', cursive;
    
}
.carous{
    
    border-radius:1%;
}
.navbar{
    background-color: rgb(138, 68, 68);
}

.searchbar{
    margin:0 auto;
    width:600px;
    box-shadow:3px 3px 3px 3px grey;
   
}
.header{
    margin:0 auto;
    text-align: center;
    margin-top:25px;
}
.card{
    box-shadow: 2px 2px 2px 2px grey;
    margin-top:50px;
    width:100%;
    height:350px;;
}
.card-text{
    text-align: center;
    background-color: gainsboro;
}
.card img{
    width:100%;
    height:70%;
}
.footer{
    background-color: black;
    margin-top: 25px;
    color: ghostwhite;
}
.logimg{
    width:100%;
    
    

}
.cartbtn{
    border-radius: 25px;
    width:125px;
    margin-left:20px;
    background-color:rgb(224, 152, 16);
}

.card-img-top{
    transition: 1s;
    
}
.card-img-top:hover{
    transform:scale(0.96);
}
.connect{
    border-radius: 50%;
    border:1px solid red;
    margin-right:5px;
}
.connect:hover{
    background: orange;
}
.services{
    color:rgb(253, 253, 251);
}
#servehead{
    background-color: rgb(197, 82, 82);
    color: ivory;
    transition: 2s;
    animation: header 2s ease-in-out infinite;
}
@keyframes header {
    from{
        transform-origin: left;
        background-color: lightcoral;
        transform: translateX(90deg);
    }
    to{
        transform-origin: right;
        background-color: lightsalmon;
        transform:translateY(-90deg)
    }
    
}
.serve{
    border-radius: 50%;
    width:150px;
    height:150px;
    background-color: white;
    transition:2s;
    animation: service 3s ease-in-out 1 ;

}
@keyframes service {
    from{
        transform-origin: center;
        transform:scale(0.25)
    }
    to{
        transform: scale(1);
    }
    
}